import axios from 'axios';
import React, { useEffect, useState } from 'react'
import AliceCarousel from 'react-alice-carousel';
import "../../styles/HomePage/Gallery.scss";


const Gallery = () => {
    const [listPhotos, setListPhotos] = useState([]);
    const [error, setError] = useState("")

    useEffect(() => {
        axios.get(process.env.REACT_APP_BACKEND_URL + `/api/products?cat=Plats`)
            .then(data => setListPhotos(data.data))
            .catch(err => setError(err.message))

    }, [])
    const responsive = {
        0: { items: 2 },
        // 400: { items: 2 },
        // 1024: { items: 3 },
    };
    const items = listPhotos.length !== 0 && listPhotos.map(({ id, image, category }, i) => {
        return (
            <div className="alice-carousel__stage-item__added">
                <a href={`/products?cat=${category}`}>
                    <div className="overlay"></div>
                    <img key={i} src={image} alt={id}></img>
                </a>
            </div>
        )
    })
    return (
        listPhotos.length === 0
            ? <div>Loading ...</div>
            : error
                ? <div>{error}</div>
                : <AliceCarousel
                    autoPlayInterval={2000}
                    autoPlay={true}
                    fadeOutAnimation={true}
                    mouseTrackingEnabled={true}
                    disableAutoPlayOnAction={true}
                    infinite
                    autoWidth
                    items={items}
                    responsive={responsive}
                    disableDotsControls
                />
    )
}

export default Gallery
